@use "../../../global/global.scss" as global;

.mainVisual {
  position: relative;
  min-height: 100vh;
  min-width: 100%;
}

.trademark {
  z-index: 2;
  position: fixed;
  margin: auto;
  height: 100%;
  width: 100%;
  &_inner {
    position: absolute;
    left: 50%;
    top: 50%;
    max-width: 600px;
    max-height: 600px;
    width: calc(100vw - 160px);
    height: calc(100vw - 160px);
    transform: translate(-50%, -50%);
  }
  &_outline {
    position: absolute;
    left: 50%;
    top: 50%;
    height: 100%;
    width: 100%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    &-1 {
      box-shadow: 0 0 1rem global.$neonColor;
      border: 0.1rem solid global.$appealColor;
    }
    &-2 {
      height: calc(100% - 20px);
      width: calc(100% - 20px);
      box-shadow: 0 0 1.5rem global.$neonColor;
      border: 0.3rem solid global.$appealColor;
    }
    &-3 {
      height: calc(100% - 50px);
      width: calc(100% - 50px);
      box-shadow: 0 0 2rem global.$neonColor;
      border: 0.5rem solid global.$appealColor;
    }
  }

  &_content {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .teamName_ttl {
    text-align: center;
    color: global.$appealColor;
    font-size: global.$teamTextSize;
    font-weight: 600;
    line-height: global.$teamTextSize;
    text-shadow: 0 0 5rem global.$neonColor;
    &-sub {
      font-weight: 600;
      font-size: global.$teamTextSubSize;
      line-height: global.$teamTextSubSize;
    }
  }
}

.catchCopy {
  z-index: 2;
  position: fixed;
  left: 50%;
  bottom: 50px;
  transform: translate(-50%, 0);
  text-align: center;
  color: global.$appealColor;
  font-size: global.$fontSizeLarge;
  font-weight: 600;
  text-shadow: 0 0 1rem global.$neonColor;
}
.bgImgArea {
  position: fixed;
  margin: 0;
  bottom: 30px;
  left: 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  max-height: 900px;
  height: calc(100vh - 60px);
}
