@use "./reset";

// Color
$backgroundColor: #1b1b1b;
$normalColor: #c7d0c6;
$appealColor: #00c060;
$neonColor: #19ff8c;

// Font Size
$fontSizeSmall: 1.4rem;
$fontSizeNormal: 1.8rem;
$fontSizeLarge: 2.4rem;

$teamTextSize: 12rem;
$teamTextSubSize: 6rem;

html {
  background-color: $backgroundColor;
  color: $normalColor;
}

section {
  position: relative;
  min-height: 100vh;
  min-width: 100vh;
}
